'use client';

import React, { useEffect, useMemo, useRef, useState } from 'react';
import { Play, Pause, SkipForward } from 'lucide-react';

declare global {
  interface Window {
    SC?: any;
  }
}

const PLAYLIST_URL = 'https://soundcloud.com/apesonape/sets/sinatra-season-by-dr-dibs';

export default function SoundCloudPlayer() {
  const iframeRef = useRef<HTMLIFrameElement | null>(null);
  const widgetRef = useRef<any>(null);
  const [isReady, setIsReady] = useState(false);
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTitle, setCurrentTitle] = useState<string>('');

  // Pick a random start track index on mount
  const startTrackIndex = useMemo(() => Math.floor(Math.random() * 10), []); // fallback upper bound; corrected after READY

  // Build iframe src with minimal classic player UI
  const playerSrc = useMemo(() => {
    const encoded = encodeURIComponent(PLAYLIST_URL);
    const params = new URLSearchParams({
      url: encoded,
      auto_play: 'false',
      hide_related: 'true',
      show_comments: 'false',
      show_user: 'true',
      show_reposts: 'false',
      show_teaser: 'false',
      visual: 'false',
      start_track: String(startTrackIndex),
    });
    return `https://w.soundcloud.com/player/?${params.toString()}`;
  }, [startTrackIndex]);

  useEffect(() => {
    let cancelled = false;

    function initWidget() {
      if (!iframeRef.current || !window.SC || !window.SC.Widget) return;
      const widget = window.SC.Widget(iframeRef.current);
      widgetRef.current = widget;

      widget.bind(window.SC.Widget.Events.READY, () => {
        if (cancelled) return;
        setIsReady(true);
        widget.setVolume(50);

        // Ensure random start within playlist length
        widget.getSounds((sounds: any[]) => {
          if (!Array.isArray(sounds) || sounds.length === 0) return;
          const randomIndex = Math.floor(Math.random() * sounds.length);
          widget.load(PLAYLIST_URL, {
            auto_play: false,
            visual: false,
            show_comments: false,
            hide_related: true,
            show_reposts: false,
            show_user: true,
            show_teaser: false,
            start_track: randomIndex,
          });
        });
      });

      widget.bind(window.SC.Widget.Events.PLAY, () => {
        if (cancelled) return;
        setIsPlaying(true);
        widget.getCurrentSound((sound: any) => setCurrentTitle(sound?.title || ''));
      });

      widget.bind(window.SC.Widget.Events.PAUSE, () => {
        if (cancelled) return;
        setIsPlaying(false);
      });

      widget.bind(window.SC.Widget.Events.FINISH, () => {
        if (cancelled) return;
        playRandomTrack();
      });
    }

    function ensureScript() {
      if (window.SC && window.SC.Widget) {
        initWidget();
        return;
      }
      const existing = document.querySelector('script[data-sc-widget]') as HTMLScriptElement | null;
      if (existing) {
        existing.addEventListener('load', initWidget);
        return;
      }
      const script = document.createElement('script');
      script.src = 'https://w.soundcloud.com/player/api.js';
      script.async = true;
      script.defer = true;
      script.setAttribute('data-sc-widget', 'true');
      script.addEventListener('load', initWidget);
      document.body.appendChild(script);
    }

    ensureScript();
    return () => {
      cancelled = true;
    };
  }, []);

  function togglePlay() {
    const widget = widgetRef.current;
    if (!widget) return;
    widget.isPaused((paused: boolean) => {
      if (paused) {
        widget.play();
      } else {
        widget.pause();
      }
    });
  }

  function nextTrack() {
    const widget = widgetRef.current;
    if (!widget) return;
    widget.next();
  }

  function playRandomTrack() {
    const widget = widgetRef.current;
    if (!widget) return;
    widget.getSounds((sounds: any[]) => {
      if (!Array.isArray(sounds) || sounds.length === 0) return;
      const randomIndex = Math.floor(Math.random() * sounds.length);
      widget.load(PLAYLIST_URL, {
        auto_play: true,
        visual: false,
        show_comments: false,
        hide_related: true,
        show_reposts: false,
        show_user: true,
        show_teaser: false,
        start_track: randomIndex,
      });
    });
  }

  return (
    <div className="fixed bottom-4 right-4 z-40 flex items-center gap-3 glass-dark border border-white/10 rounded-xl p-3 shadow-lg">
      {/* Hidden/minimal iframe player */}
      <iframe
        ref={iframeRef}
        title="Apes On Ape â€” SoundCloud Player"
        style={{ width: 0, height: 0, opacity: 0, pointerEvents: 'none', position: 'absolute' }}
        src={playerSrc}
        allow="autoplay"
      />

      {/* Simple Controls */}
      <button
        onClick={togglePlay}
        className="p-2 rounded-lg bg-hero-blue/10 hover:bg-hero-blue/20 transition-colors"
        aria-label={isPlaying ? 'Pause' : 'Play'}
        disabled={!isReady}
      >
        {isPlaying ? <Pause className="w-5 h-5 text-hero-blue" /> : <Play className="w-5 h-5 text-hero-blue" />}
      </button>
      <button
        onClick={nextTrack}
        className="p-2 rounded-lg bg-hero-blue/10 hover:bg-hero-blue/20 transition-colors"
        aria-label="Next"
        disabled={!isReady}
      >
        <SkipForward className="w-5 h-5 text-hero-blue" />
      </button>

      <div className="max-w-[14rem] truncate" style={{ color: 'var(--foreground)' }}>
        {currentTitle || 'SoundCloud Player'}
      </div>
    </div>
  );
}


